import {React, useState, useEffect} from 'react'
import {CRow, CCol, CFormGroup, CLabel, CSelect, CTextarea, CInput} from '@coreui/react'
import AsyncSelect from 'react-select/async';
import Select from 'react-select'
import axios from 'axios'
import supabase from '../../../../../supabase'
import { useDispatch, useSelector } from 'react-redux';

const Garam = (props) =>{
    const [data, setData] = useState({})
    const dispatch = useDispatch()
    const [option, setOption] = useState({})
    const komoditas = useSelector((state)=>state.komoditas)
    const jnsdata = useSelector((state)=>state.jnsdata)
    const [disabledBulan, setDisabledBulan] = useState(false)
    const [disabledProv, setDisabledProv] = useState(false)    
    const arrBulan = ["","Januari","Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"]
    const handleSearchProvinsi = async(namaPropinsi) =>{ 
        return axios.get('https://dev.farizdotid.com/api/daerahindonesia/provinsi').then((x)=>{
            let filtered = x.data.provinsi.map((x)=>{
                return(
                    {['label']:x.nama, ['value']:x.id}
                )
            }).filter((x)=>x.label.toLowerCase().includes(namaPropinsi.toLowerCase()))
            console.log(filtered)
            return filtered
        })
    }
    useEffect(async() => {
        if(props.modal){
            let { data: tr_subkomoditas, error } = await supabase
            .from('tr_ref_nk')
            .select("*")
            .eq('kd_parent', komoditas.kode)
            let filtered = tr_subkomoditas.map((x)=>{
                let disabled = false
                if(jnsdata.kode === '01'){
                    if(x.value == '0202'){
                        disabled = 'no'
                    } else {
                        disabled = 'yes'
                    }                    
                } else {
                    if(x.value == '0202'){
                        disabled = 'no'
                    } else {
                        disabled = 'yes'
                    }                                         
                }
                return(
                    {['value']:x.value, ['label']:x.label,['disabled']:disabled}
                )
            })
            // .filter((x)=>x.value.slice(0,2) == komoditas.kode)
            console.log(filtered)
            console.log(jnsdata.kode)
            setOption({...option,['subkomoditas']:filtered})            
        }      
    },[props.modal]);
    useEffect(async() => {
        dispatch({type: 'set', data: {...data} })  
    },[data]);    
    useEffect(async() => {
        if(!props.modal){
            console.log("Tutup")
            setData({
                'periode':'',
                'bulan':'',
                'propinsi':'',
                'kota':'',
                'urbarang':'',
                'jmlbarang':'',
                'subkomoditas':'',
                'jnsguna':'',
                'jnsbarang':'',
                'ktgrbarang':'',
                'varians':'',
                'stdmutu':'',
                'hscode':'',
                'satuan':''
            })
            setDisabledProv(false)
            setDisabledBulan(false)
            console.log(data)
            setOption({})
        } else {
            console.log(data)
        }
    },[props.modal]);    
    const handleChange = async (e,index) =>{
        if(index){
            if (index.name == 'propinsi'){
                setData({...data, [index.name]:e, ['kota']:''})
                axios.get('https://dev.farizdotid.com/api/daerahindonesia/kota?id_provinsi='+e.value).then((x)=>{
                    let kotakab = x.data.kota_kabupaten.map((x)=>{
                        return(
                            {['value']:x.id, ['label']:x.nama}                     
                        )
                    })
                    console.log(kotakab)
                    setOption({...option, ['kota']:kotakab})
                })       
            } else if (index.name == 'kota'){
                setData({...data, [index.name]:e}) 
            } else if (index.name == 'subkomoditas'){
                setData({...data, [index.name]:e,['jnsguna']:'',['jnsbarang']:'',['stdmutu']:'',['hscode']:''})
                let jnsguna = await supabase
                .from('tr_ref_nk')
                .select("*")
                .eq('kd_parent', e.value)
                .eq('kd_grup', '03')
                let filteredjnsguna = jnsguna.data.map((x)=>{
                    let disabled = false
                    if(jnsdata.kode === '01'){
                        if(x.value == '020203A'){
                            disabled = 'no'
                        } else {
                            disabled = 'yes'
                        }                                          
                    } else {
                        let arrEnable = ['020201A']              
                        if(arrEnable.includes(x.value)){
                            disabled = 'no'
                        } else {
                            disabled = 'yes'
                        }                     
                    }       
                    return(
                        {['value']:x.value, ['label']:x.label,['disabled']:disabled}
                    )
                })
                // .filter((x)=>x.value.slice(0,4) == e.value)
                let jnsbarang = await supabase
                .from('tr_ref_nk')
                .select("*")
                .eq('kd_parent', e.value)
                .eq('kd_grup', '04')
                let filteredjnsbarang = jnsbarang.data.map((x)=>{
                    let disabled  = false              
                    // let arrEnable = ['020201B', '020202B', '020203B', '020204B', '020205B' , '020206B']                
                    // if(jnsdata.kode === '01'){
                    //     if(arrEnable.includes(x.kd_stdmutu)){
                    //         disabled = 'no'
                    //     } else {
                    //         disabled = 'yes'
                    //     }                    
                    // } else {
                    //     disabled = 'no'
                    // }              
                    return(
                        {['value']:x.value, ['label']:x.label,['disabled']:disabled}
                    )
                })
                // .filter((x)=>x.value.slice(0,4) == e.value)            
                setOption({...option,['jnsguna']:filteredjnsguna,['jnsbarang']:filteredjnsbarang,['stdmutu']:[],['hscode']:[]})             
            } else if (index.name == 'jnsguna'){
                setData({...data, [index.name]:e,['stdmutu']:''})                
                let stdmutu = await supabase
                .from('tr_ref_nk')
                .select("*")
                .eq('kd_parent', e.value)
                .eq('kd_grup', '07')   
                let filteredstdmutu = stdmutu.data.map((x)=>{
                    let disabled  = false              
                    // let arrEnable = ['020201B', '020202B', '020203B', '020204B', '020205B' , '020206B']                
                    // if(jnsdata.kode === '01'){
                    //     if(arrEnable.includes(x.kd_stdmutu)){
                    //         disabled = 'no'
                    //     } else {
                    //         disabled = 'yes'
                    //     }                    
                    // } else {
                    //     disabled = 'no'
                    // }              
                    return(
                        {['value']:x.value, ['label']:x.label,['disabled']:disabled}
                    )
                })                             
                setOption({...option,['stdmutu']:filteredstdmutu})            
            } else if (index.name == 'jnsbarang'){
                setData({...data, [index.name]:e, ['hscode']:''})
                let { data: tr_hscode, error } = await supabase
                .from('tr_ref_nk')
                .select("*")
                .eq('kd_parent', e.value)
                let filtered = tr_hscode.map((x)=>{
                    return(
                        {['value']:x.value, ['label']:x.label}
                )
                })
                // .filter((x)=>x.value.slice(0,7) == e.value)
                setOption({...option,['hscode']:filtered})             
            } else {
                setData({...data, [index.name]:e})
            }            
        } else {
            setData({...data, [e.target.name]:e.target.value})            
        }        
    } 
    const handleSearchSatuan = (keyword) =>{
        return axios.get('https://api.insw.go.id/api/ref/satuan').then((x)=>{
            let filtered = x.data.data.map((x)=>{
                return(
                    {['label']:x.urSatuan, ['value']:x.kodeSatuan}
                )
            }).filter((x)=>x.label.toLowerCase().includes(keyword.toLowerCase()))   
            return filtered       
        })
    }      
    const handleCheck = (x) =>{
        if(x.target.name == 'propinsi'){
            setDisabledProv(x.target.checked)
            if(x.target.checked){
                setData({...data,['propinsi']:'All', ['kota']:'All'})
            }         
        } else {
            setDisabledBulan(x.target.checked)
            if(x.target.checked){
                setData({...data,['bulan']:'All'})
            }   
        }      
    }     
    return(
        <>
        <CRow>
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Periode</CLabel>
                    <CSelect value={data.periode} onChange={(e)=>handleChange(e)} name="periode">
                      <option value=""></option>
                      <option value="2022">2022</option>
                      <option value="2023">2023</option>                        
                    </CSelect>
                </CFormGroup>
            </CCol>
            <CCol md="2">
                <CFormGroup>
                    <CLabel>Bulan [ <div style={{display:'inline-block'}}><input checked={disabledBulan} type="checkbox" name="bulan" onChange={(x)=>handleCheck(x)}/> All</div> ]</CLabel>
                    <CSelect value={data.bulan} onChange={(e)=>handleChange(e)} name="bulan" disabled={disabledBulan}>
                      {
                          arrBulan.map((x, index)=>{
                              return(
                                    <option value={index+1}>{x}</option>                                  
                              )
                          })
                      }
                    </CSelect>
                </CFormGroup>
            </CCol>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Provinsi [ <div style={{display:'inline-block'}}><input checked={disabledProv} type="checkbox" name="propinsi" onChange={(x)=>handleCheck(x)}/> All</div> ]</CLabel>
                    <AsyncSelect isDisabled={disabledProv} name="propinsi" value={data.propinsi} onChange={(e,index)=>handleChange(e,index)} loadOptions={(x)=>handleSearchProvinsi(x)} />                    
                </CFormGroup>
            </CCol> 
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Kota</CLabel>
                    <Select isDisabled={disabledProv} value={data.kota} onChange={(e,index)=>handleChange(e,index)} name="kota" options={option.kota} />                    
                </CFormGroup>
            </CCol>                                    
        </CRow>
        <CRow>
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Sub Komoditas</CLabel>
                    <Select isOptionDisabled={(option) => option.disabled === 'yes'} name="subkomoditas" value={data.subkomoditas} onChange={(e,index)=>handleChange(e,index)} options={option.subkomoditas} />
                </CFormGroup>
            </CCol> 
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Jenis Penggunaan</CLabel>
                    <Select isOptionDisabled={(option) => option.disabled === 'yes'} name="jnsguna" value={data.jnsguna} onChange={(e,index)=>handleChange(e,index)} options={option.jnsguna}/>
                </CFormGroup>
            </CCol>   
            <CCol md="4">
                <CFormGroup>
                    <CLabel>Standar Mutu</CLabel> 
                    <Select isOptionDisabled={(option) => option.disabled === 'yes'} name="stdmutu" value={data.stdmutu} onChange={(e,index)=>handleChange(e,index)} options={option.stdmutu}/>                   
                </CFormGroup>
            </CCol>                              
        </CRow>  
        <CRow>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Jenis Barang</CLabel>
                    <Select name="jnsbarang" value={data.jnsbarang} onChange={(e,index)=>handleChange(e,index)} options={option.jnsbarang}/>                    
                </CFormGroup>
            </CCol>            
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Kode HS</CLabel>
                    <Select name="hscode" value={data.hscode} onChange={(e,index)=>handleChange(e,index)} options={option.hscode}/>                    
                </CFormGroup>
            </CCol>
            <CCol md="6">
                <CFormGroup>
                    <CLabel>Uraian Barang</CLabel>
                    <CTextarea value={data.urbarang} onChange={(e)=>handleChange(e)} name="urbarang"/>                   
                </CFormGroup>
            </CCol>            
        </CRow> 
        <CRow>
            <CCol md="6">
                <CFormGroup>
                    <CLabel>Jumlah Barang</CLabel>
                    <CInput value={data.jmlbarang} onChange={(e)=>handleChange(e)} name="jmlbarang" type="number"/>                   
                </CFormGroup>
            </CCol>
            <CCol md="6">
                <CFormGroup>
                    <CLabel>Satuan Barang</CLabel>
                    <AsyncSelect name="satuan" value={data.satuan} onChange={(e,index)=>handleChange(e,index)} loadOptions={(x)=>handleSearchSatuan(x)} />                 
                </CFormGroup>
            </CCol>            
        </CRow>        
        </>   
    )
}

export default Garam