import React, { lazy, useEffect, useState } from 'react'
import {
  CCard,
  CCardBody,
  CLink,
  CButton,
  CLabel
} from '@coreui/react'
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import supabase from '../../../../supabase'

const FormCheckpoint = (props) => {
    const [check, setCheck] = useState(false)
    const history = useHistory()
    const handleCheck = () =>{
        setCheck(!check)
    }
    const ajubaru = useSelector((state)=>state.ajubaru)
    const jenisPerijinan = useSelector(state=>state.jenisPerijinan)
    const handleKirim = async() =>{
        const { data, error } = await supabase
        .from('td_data')
        .update({ kd_status: '02' })
        .eq('id', ajubaru[0].id)        
        history.push('/pasokankebutuhan')
    }
  return (
      <>
      <CCard style={{boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)', borderRadius:'12px'}}>
          <CCardBody>
              <div className="d-flex flex-row justify-content-start pb-3">
                <div className="pr-3">
                    <i class="fas fa-file"></i>
                </div>
                <div>
                    <h5>Checkpoint Kelengkapan Data</h5>
                </div>
              </div>
              <div className="d-flex flex-row justify-content-center">                
                  <div className="col">
                      <CCard>
                          <CCardBody>
                              <div className="d-flex flex-row">
                                <div style={{color:"green"}} className="pr-5">
                                    <i class="fas fa-check fa-3x"></i>
                                </div>
                                <div>
                                    <h6>Data Komoditas</h6>
                                    <small>Data Lengkap</small>
                                </div>                                  
                              </div>
                          </CCardBody>
                      </CCard>
                  </div>                                  
              </div>
              <div className="d-flex">
                  <CCard style={{width:"100%"}}>
                      <CCardBody>
                          <div className="d-flex flex-column justify-content-start">
                          <div>
                                  <h5><b>Disclaimer</b></h5>
                              </div>
                              <div>
                                  <h6>1. Dengan ini saya menyatakan bertanggung jawab atas kebenaran hal-hal yang diberitahukan dalam dokumen ini</h6>
                              </div>
                          </div>
                      </CCardBody>
                  </CCard>
              </div>
              <div className="d-flex justify-content-between">
                <div className="d-flex flex-row">
                        <div className="pr-2">
                            <input value={check} onClick={()=>handleCheck()} type="checkbox"/>                          
                        </div>
                        <div>
                            <CLabel>Saya Menyetujui</CLabel>                          
                        </div>                          
                  </div>
                {/* <div>
                <i class="fas fa-file"></i> Cetak Laporan
                </div>                   */}
              </div>
                <div className="d-flex flex-row justify-content-end">
                    <div className="d-flex mr-2">
                        <CButton onClick={()=>history.push('/pasokankebutuhan')} style={{borderRadius:'12px', backgroundColor:'#517fa4', color:'white'}} className="btn clickable"><i class="far fa-save"></i> Simpan Draft</CButton>
                    </div>
                    <div>
                        <CLink style={{borderRadius:'12px'}} disabled={!check} role="button" onClick={()=>handleKirim()} className="btn btn-success clickable"><i class="fas fa-paper-plane"></i> Kirim Data</CLink>
                    </div>
                </div>              
          </CCardBody>
      </CCard>
      </>
  )
}

export default FormCheckpoint
